@import "../SCSS/Variables.scss";
@import "../SCSS/Mixins.scss";

.header {
    display: flex;
    justify-content: space-between;
    margin-top: 2vh;
}
.headerNavbar {
    width: 234px;
    display: flex;
    justify-content: space-around;
}
.navbarItem a {
    color: black;
    font-size: 16px;
    text-decoration: none;
    transition: .2s;
    &:hover {
        color: $primary_color;
        &::after {
            content: "";
            display: block;
            margin-top: 3vh;
            width: 100%;
            background-color: $primary_color;
            height: 2px;
        }
    }
}
.mainLogo {
    cursor: pointer;
}
.headerCartBar{
    width: 100px;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
}
.currency {
    position: relative;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    &::after {
        content: "";
        position: absolute;
        top: 37.5%;
        right: -1vw;
        font-size:3px;
        padding: 3px;
        display: inline-block;
        border-right: 1px solid black;
        border-top: 1px solid black;
        transform: rotate(135deg);
    }
}
.activeCurrency {
    &::after {
        top: 50%;
        transform: rotate(315deg);
    }
}
.cartWrapper {
    position: relative;
    cursor: pointer;
}
.cartBadge {
    position: absolute;
    display: flex;
    top: -10px;
    right: -10px;
    width: 20px;
    height: 20px;
    color: white;
    background: black;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-family: "Roboto", sans-serif;
    font-size: 14px;
}
.cartBadgeContent {
    display: inline-block;
}

//use mixin (with the same styles) for overlay on cart component and currency component
.modal {
    @include modal_overlay(rgba(0, 0, 0, 0.1))
}
.active {
    opacity: 1;
    pointer-events: all;
    z-index: 99;
}
.modalContent {
    width: 325px;
    height: 540px;
    padding: 6px 10px 0px 10px;
    background: white;
    margin-right: 8vw;
    overflow: scroll;
}
.currencyContent {
    width: 114px;
    height: 269px;
    margin-right: 7vw;
    padding-left: 20px;
    background: white;
    -webkit-box-shadow: 0px 0px 13px 5px rgba(0,0,0,0.28); 
    box-shadow: 0px 0px 13px 5px rgba(0,0,0,0.28);
    font-weight: 500;
    font-size: 18px;
    line-height: 28.8px;
    & div {
        padding-top: 20px;
        cursor: pointer;
        &:hover {
            color: $primary_color;
            transition: .2s;
        }
    }
}
// Cart inner styles
.cartContainer {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.cartFlexContainer {
    width: 139px;
    display: flex;
    justify-content: space-between;
}
.verticalAlign{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.cartItemPhoto img {
    max-width: 105px;
    max-height: 137px;
}
.cartCounter {
    width: 24px;
    height: 24px;
}
.cartBtns {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
}
.attrWrapper {
    display: flex;
}
.colorAttr {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    border: 1px solid black;
    cursor: pointer;
}
.itemAttr {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    border: 1px solid black;
    cursor: pointer;
    font-size: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
        background: black;
        color: white;
        transition: .3s;
    }
}
.cartItemName {
    font-weight: 300;
    font-size: 16px;
    line-height: 25.6px;
}
.attrName {
    font-weight: 300;
    font-size: 16px;
    line-height: 25.6px;
}
.cartItemPrice {
    font-weight: 500;
    font-size: 16px;
    line-height: 25.6px;
}
.totalPriceBlock {
    display: flex;
    justify-content: space-between;
}
.total {
    font-size: 16px;
    font-weight: 500;
    
}
.totalPrice {
    font-size: 16px;
    font-weight: 700;
}
.cartBtnsWrap {
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
}
.viewBagBtn {
    width: 140px;
    height: 43px;
    border: 1px solid black;
    background: white;
    cursor: pointer;
    font-weight: 600;
    transition: .3s;
    &:hover {
        color: $primary_color;
    }
}
.checkOutBtn {
    width: 140px;
    height: 43px;
    border: none;
    background: $primary_color;
    color: white;
    cursor: pointer;
    font-weight: 600;
    transition: .3s;
    &:hover {
        color: black;
    }
}
